<template>
  <div class="insertorder">
        <div class="contaniar">
            <div class="row">
                <div class="col-md-4">

                </div>
                <div class="col-md-5">
                    <div v-for="(item,index) in addinsert" :key="item">
                        <!-- <inserttable ref="getitem" /> -->
                        <!-- {{addinsert}} -->
                        <div class="insertblock">
                            <div class="col-md-12 divblock">
                                <label for="">商品名稱：</label>
                                <input v-model="addorder[index].name" type="text">
                            </div>
                            <div class="col-md-12 divblock">
                                <label for="">圖示連結：</label>
                                <input type="file" id="customFile" class="form-control"
                                    ref="files" @change="uploadFile($event,index)">
                            </div>
                            <div class="col-md-12 divblock">
                                <label for="">訂單狀態：</label>
                                <select v-model="addorder[index].status" class="custom-select" id="inputGroupSelect01">
                                    <option value="0">select</option>
                                    <option value="1">進行中</option>
                                    <option value="2">已完成</option>
                                </select>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-md-2 divblock">
                    <button class="addbtn btn btn-primary" @click="btnadd()">
                        +<!-- <i class="fas fa-plus"></i> -->
                    </button>
                </div>
                <div class="col-md-2 btninsert">
                    <button class="btn btn-outline-dark" @click="btninsert()">
                        新增
                    </button>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
import inserttable from './inserttable'
export default {
    components:{
        inserttable
    },
    data(){
        return{
            addinsert:1,
            oldorder:'',
            status:0,
            addorder:[{
                name:'',
                logo:'',
                status:''
                }
            ],
            neworder:[

            ]
        }
    },
    methods:{
        uploadFile(e,index){
            this.addorder[index].logo = e.srcElement.files[0].name;
        },
        btnadd(){
            this.addinsert++;
            this.addorder.push({
                name:'',
                logo:'',
                status:{
                    code:'',
                    type:'',
                },
                date:'',
            });
        },
        btninsert(){
            // console.log(this.oldorder);
            // console.log(this.addorder);
            this.addorder.forEach(item => {
                switch (item.status){
                    case "1":{
                        item.status={
                            code:1,
                            type:'處理中'
                        }
                        break;
                    }
                    case "2":{
                        item.status={
                            code:2,
                            type:'已成立'
                        }
                        break;
                    }
                    case "3":{
                        item.status={
                            code:3,
                            type:'已取消'
                        }
                        break;
                    }
                    case "4":{
                        item.status={
                            code:4,
                            type:'已送達'
                        }
                        break;
                    }
                }
                var year = new Date().getFullYear()-1911;
                var mount = new Date().getMonth();
                var day = new Date().getDate();
                item.date = year+'/'+mount+'/'+day;

            });
            // console.log(this.oldorder.orders);
            this.neworder = this.oldorder.orders.concat(this.addorder);
            // console.log(this.neworder);
            // console.log(this.oldorder.orders);
            // console.log(this.addorder);
            // console.log(this.neworder);
            this.$store.dispatch("getactions_order",this.neworder);
            alert("新增成功");
            // console.log(this.oldorder);
        }
    },
    created(){
        this.oldorder = this.$store.getters.getgetters_order;  
    }
}
</script>

