<template>
  <div class="container-fill login">
      <div class="row">
          <div class="login_border">
                <div class="logintext">
                    <span>登入</span>
                </div>
                <div class="logininput">
                    <form  @submit.prevent="login">
                        <div class="form-group" :class="{ 'form-group--error': $v.form.email.$error }" >
                                <input type="text" class="form-control" name="Email" id="useremail" 
                                    v-model="form.email"  placeholder="輸入帳號" @blur="$v.form.email.$touch()">
                        </div>
                        <div style="margin-bottom:10px" class="error text-danger" v-if="$v.form.email.$error && !$v.form.email.required || !$v.form.email.email">請輸入帳號</div>
                        <div class="form-group" :class="{ 'form-group--error': $v.form.pw.$error }" >
                                <input type="text" class="form-control" name="Email" id="useremail"
                                    v-model="form.pw"  placeholder="輸入密碼" @blur="$v.form.pw.$touch()">
                        </div>
                        <div style="margin-bottom:10px" class="error text-danger" v-if="$v.form.pw.$error && !$v.form.pw.required || !$v.form.pw.numeric">請輸入密碼</div>
                        <div class="login-input-m-l">
                            <button class="btn btn-secondary">Login</button>
                        </div>
                    </form>
                    <div class="fbbtn">
                        <button class="btn btn-secondary" @click="fblogin()"><i class="fab fa-facebook-f"></i></button>
                    </div>
                </div>
          </div>
      </div>
  </div>
</template>

<script>
import { required,email,numeric } from 'vuelidate/lib/validators'
export default {
    data(){
        return{
            form:{
                email:'',
                pw:'',
            },
        }
    },
    validations: {
        form:{
            email:{
                required,
                email
            },
            pw:{
                required,
                numeric
            },
        }
    },
    methods:{
        login(){
            // console.log(this.form); 
            var vm=this;
            if(this.form.pw==='12345'){
                vm.$router.push('/main');
            }
            else{
                alert("帳號密碼錯誤");
            }
        },
        getProfile(){
            let vm =this;
            FB.api('/me?fileds=name,id,email',function(res){
                console.log('res in graphAPI',res);
                if(res.name!==""){
                    vm.$store.dispatch("getactions_name",res.name);
                    // vm.$router.push('/main');
                    FB.getLoginStatus( res =>{
                        if(res.status=="connected"){
                            vm.$router.push('/main');
                        }
                    })
                }
            })

        },
        fblogin(){
            let vm =this;
            FB.login(function(res){
            console.log('res',res);
                vm.getProfile();
            //     vm.$router.push('/main');
            // }
            },{
            scope:'email,public_profile',
            return_scope:true
            })
        },
    },
    mounted(){
        window.fbAsyncInit = function() {
        FB.init({
            appId      : '1189032308216926',
            cookie     : true,
            xfbml      : true,
            version    : 'v10.0'
        });
            
        FB.AppEvents.logPageView();   

        FB.getLoginStatus( res =>{
            console.log('res',res);
        })
        };
  }
}
</script>

