import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/components/Login.vue'
import Main from '@/components/Main.vue'
import selectorder from '@/components/selectorder.vue'
import insertorder from '@/components/insertorder.vue'

Vue.use(VueRouter)

const routes = [
  {
    path:'*',
    redirect:'login',//如果導向沒有的路由則會導回login
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/main',
    name: 'Main',
    component: Main,
    children:[
      {
        path: '/selectorder',
        name: 'selectorder',
        component: selectorder,
      },
      {
        path: '/insertorder',
        name: 'insertorder',
        component: insertorder,

      },
    ]
  },
  // {
  //   path: '/selectorder',
  //   name: 'selectorder',
  //   component: selectorder,
  // },
  // {
  //   path: '/insertorder',
  //   name: 'insertorder',
  //   component: insertorder,

  // },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
