<template>
  <div class="selectorder">
      <div class="container">
          <div class="row">
              <div class="col-md-6">
                    <div class="input-group mb-3">
                        <div class="input-group-prepend">
                            <label class="input-group-text" for="inputGroupSelect01">訂單狀態：</label>
                        </div>
                        <select v-model="status" class="custom-select" id="inputGroupSelect01">
                            <option value="0">select</option>
                            <option value="1">進行中</option>
                            <option value="2">已完成</option>
                        </select>
                    </div>
                    <div>
                        <button v-if="checkedItems!=''" class="btn btn-outline-danger" @click="btncancel()">
                            取消訂單
                        </button>
                    </div>
              </div>
              <div class="col-md-6">
                  <!-- {{status}} -->
                  <!-- {{checkedItems}} -->
                  <table v-if="status=='1'|| status=='0'">
                      <thead>
                          <th colspan="4">進行中</th>
                      </thead>
                      <tbody>
                          <template v-for="(order,index) in filterdata"  >
                              <!-- <tr  
                              :class="{ active : selectorder.includes(matchname) }"  -->
                              <!-- <tr  
                              :class="{ active: orders.includes(order.name) }"
                              @click="selectitem(order)" :key="order.name" v-if="order.status.code == '1' || order.status.code == '2' "> -->
                              <tr 
                                @click="selItem(order)"
                                :class="{ active: opened.includes(order.name) }" 
                                :key="order.name" 
                                v-if="order.status.code == '1' || order.status.code == '2' ">
                                  <td hidden>
                                        <div>
                                        <input type="checkbox"
                                                :id="'item'+index"
                                                :value="order"
                                                v-model="checkedItems">
                                        </div>
                                 </td>
                                  <td>
                                      <img :src="order.logo" alt="">
                                  </td>
                                  <td>
                                        <div class="row">
                                            <div class="col-md-6" style="color:green">{{order.status.type}}</div>
                                            <div class="col-md-6">預計出貨：{{order.date}}</div>
                                        </div>
                                        
                                        
                                        <br>
                                        <div class="divname">
                                            {{order.name}}
                                        </div>
                                      
                                  </td>
                                  <td style="width:20px">
                                      <i class="fas fa-angle-right"></i>
                                  </td>
                              </tr>
                          </template>
                      </tbody>
                  </table>
                  <table v-if="status=='2' || status=='0'">
                      <thead>
                          <th colspan="3">已完成</th>
                      </thead>
                      <tbody>
                          <template v-for="order in filterdata" >
                              <tr  :key="order.name" v-if="order.status.code == '3' || order.status.code == '4'" >
                                  <td>
                                        <img style="filter:grayscale(100%);" :src="order.logo" alt="">
                                  </td>
                                  <td>
                                        <div class="row">
                                                <div class="col-md-6" style="color:green">{{order.status.type}}</div>
                                                <div class="col-md-6">預計出貨：{{order.date}}</div>
                                            </div>
                                        <br>
                                        <div class="divname">
                                            {{order.name}}
                                        </div>
                                  </td>
                                  <td style="width:20px">
                                        <i class="fas fa-angle-right"></i>
                                  </td>
                              </tr>
                          </template>
                      </tbody>
                  </table>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
export default {
    data(){
        return{
            orders: [],
            // selectorder:[],
            match:'',
            status:0,
            opened:[],
            checkedItems:[]
        }
    },
    methods:{
        // selectitem(order){
        //     if(this.selectorder == ""){
        //         this.selectorder.push(order.name);
        //         this.match=order.name;
        //     }
        //     else{
        //         var ans = this.selectorder.some((item) => {
        //             return item === order.name;
        //         });
        //         if(!ans){
        //             this.selectorder.push(order.name);
        //             this.match=order.name;
        //         }
        //         else{
        //             this.selectorder = this.selectorder.filter(function(item) {
        //                 return item != order.name;
        //             });
        //             this.match=order.name;
        //         }
        //     }
        // },
        selItem(order){
            const index = this.opened.indexOf(order.name);
            if(index > -1){
                this.opened.splice(index,1);
                this.checkedItems.splice(index,1);
            } else {
                this.opened.push(order.name);
                this.checkedItems.push(order);
            }
        },
        btncancel(){
            this.orders.forEach(item => {
                if(this.opened.includes(item.name)){
                    // console.log(item);
                    item.status.code = 3;
                    item.status.type = "已取消";
                }
            });
            this.$store.dispatch("getactions_order",this.orders);
            alert("取消成功");
            this.orders = [];
            this.checkedItems = [];
            this.orders = this.$store.getters.getgetters_order.orders;

            // console.log(this.orders);
            // console.log(this.opened);
        }
    },
    computed:{
        // matchname(){
        //     var filter = this.selectorder.filter((item, index, array)=>{
        //         return item === this.match;       
        //     });
        //     console.log(filter);
        //     return filter[0];
        // },
        filterdata(){
            this.orders.forEach(item => {
                var d1 = new Date(item.date);
                item.date = d1;
                
            });
            let sortdate =  this.orders.sort((d1, d2) => d1.date - d2.date);
            sortdate.forEach(item => {
                var year = new Date(item.date).getFullYear();
                var mount = new Date(item.date).getMonth()+1;
                var day = new Date(item.date).getDate();
                item.date = year+'/'+mount+'/'+day;
            });
            return sortdate;
        },
    },
    created(){
        this.orders = this.$store.getters.getgetters_order.orders;
    }
}
</script>

